import {FlashMessagesService} from 'angular2-flash-messages';
import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service'
import {ValidateService} from '../../services/validate.service'
import {Router} from '@angular/router';

@Component({

  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
name:String;
username:String;
email:String;
password:String;


  constructor(
  private validateService:ValidateService,
  private flashMessage:FlashMessagesService,
  private authService:AuthService,
  private router:Router) { }

  ngOnInit() {
  }

// making shrr the user entered correct stuff.
//using validateRegister and validateEmail which are defined in services/validate.service.ts

onRegisterSubmit(){
const user = {
name: this.name,
email: this.email,
username: this.username,
password: this.password
}
if(!this.validateService.validateRegister(user)){
this.flashMessage.show('fill all the fields',{cssClass:'alert-danger',timeout:7000});
return false;
}

if(!this.validateService.validateEmail(user.email)){
this.flashMessage.show('incorrect Email format.',{cssClass:'alert-danger',timeout:5000});
return false;


}
// Register user
this.authService.registerUser(user).subscribe(data => {
if (data.success){

this.flashMessage.show('Registration successful',{cssClass:'alert-success',timeout:7000});
this.router.navigate(['/login']);
}else{

this.flashMessage.show('An error occurred',{cssClass:'alert-danger',timeout:7000});
this.router.navigate(['/register']);

}
});
}
}



